import React, { useState, useRef, useContext, useEffect } from 'react';
import './styles/createPost.css';
import axios from 'axios';
import {ApiURL} from './urls'
import { useParams } from 'react-router-dom/cjs/react-router-dom.min';
import Alert from './alert';

function _date() {
    let t = Date.now();
    let d = new Date(t);
    d = d.toUTCString();
    d = d.slice(0, d.length-4);
    return d;
}


function CreatPost({variant}){
    const [post, setPost] = useState({
        title: '',
        tags: [],
        body: '',
        completed: false,
        author: 'dare'
    });
    const [alert, setAlert] = useState({variant: "", message: "", show: false});
    let apiurl = useContext(ApiURL);
    let {id} = useParams();



    useEffect(() => {
        if (variant === "edit") {
            axios.get(`${apiurl}/posts/${id}`)
              .then(res => {
                  if(res.data.hasOwnProperty('error')){
                      throw new Error(res.data.error)
                  }else {
                      let d = res.data;
                      setPost({
                          title: d.title, 
                          tags: d.tags,
                          body: d.body,
                          completed: d.completed,
                          author: d.author
                      });
                      
                  }
              })
              .catch(err => console.log(err))
        }else {
            return;
        }
    }, [apiurl, id, variant]);

    useEffect(() => {
        if (alert.show) {
            setTimeout(() => {
                setAlert({variant: "", show: false, message: ""})
            }, 5000);
        }
    }, [alert]);
    const titleRef = useRef(null);
    const tagRef = useRef(null);
    const bodyRef = useRef(null);
    const completeRef = useRef(null);

    function handleTitle() {
        setPost({...post, title: titleRef.current.value})
    }
    function handleBody() {
        setPost({...post, body: bodyRef.current.value})
    }
    function handleTags() {
        let tag = tagRef.current.value;
        tag = tag.toLowerCase()
        if (tag === '') {
            return
        }else if (post.tags.includes(tag)) {
            tagRef.current.value = '';
        }else{
            let newTag = [...post.tags, tag]
            setPost({...post, tags: [...newTag] });
            tagRef.current.value = '';
        }
    }
    function handleComplete() {
        setPost({...post, completed: completeRef.current.checked });
    }
    function removeTag(d){
        let tag = d;
        if (post.tags.includes(tag)) {
            let tags = [...post.tags];
            let index = post.tags.indexOf(tag);
            tags.splice(index, 1);
            setPost({...post, tags: tags});
        }else {
            console.log('no');
        }
    }
    function showAlert(variant, message) {
        setAlert({variant, message, show: true})
    }
    function handleSave() {
        localStorage.setItem(`post${id}`, JSON.stringify(post));
        showAlert("success", "Saved successfully")
    }


    async function handlePost() {
        if (post.title.length < 3 || post.body.length < 10) {
            window.alert('please input something good in the title and body fields before posting')
        }else {
            if (variant === "edit") {
                const res = await axios.patch(`${apiurl}/posts/${id}`, {...post});
                showAlert("success", "Updated")
                console.log(res.data);
                return;
            }
            const res = await axios.post(`${apiurl}/posts`, {...post});
            showAlert("success", "Posted")
            console.log(res.data)
            
            // submit the post
        }
    }


    return ( 
        <div id="c-post">
            {alert.show && <Alert variant={alert.variant}>
                {alert.message}
            </Alert>}
            <div className="post-input">
{///////////////////////   save icon    //////////////////////
}
                <svg onClick={handleSave} viewBox="0 0 16 16" id="save" fill="white" xmlns="http://www.w3.org/2000/svg" width="60" height="60">
                    <path  d="M13.353 1.146L14.853 2.646L15 3V14.5L14.5 15H1.5L1 14.5V1.5L1.5 1H13L13.353 1.146ZM2 2V14H14V3.208L12.793 2H11V6H4V2H2ZM8 2V5H10V2H8Z" fill="white"/>
                </svg>

                
{///////////////////////   title-input    //////////////////////
}
                <label className="create-post-label" htmlFor="post-title">post title: </label>
                <input className="create-post-input" onChange={handleTitle} ref={titleRef} type="text" value={post.title} name="post-title" id="post-title" placeholder="Add post title" />
{///////////////////////   tags-input    //////////////////////
}
                <form onSubmit={e => {e.preventDefault(); handleTags()}}  className="input-tags">
                    <label className="create-post-label" htmlFor="tags-input">Tag: </label>
                    <input className="create-post-input" type="text" ref={tagRef} name="tags-input" id="tags-input" placeholder="Add a tag" />
                </form>
{//////////////////   complete and post    //////////////////////
}
                <div className="flex">
                    <div className="complete-wrapper">
                        <label className="create-post-label" htmlFor="complete">
                            Post completed
                        </label>
                        <input type="checkbox" checked={post.completed} onChange={handleComplete} ref={completeRef} name="complete" id="complete"/>
                    </div>
                    <button id="post" onClick={handlePost} >{variant ===  "edit" ? <span style={{marginLeft: "-4px", fontSize: "0.9rem"}}>update</span> : "post"}</button>                
                </div>
{///////////////////////   tags-view    //////////////////////
}
                <div className="tags-view">
                    {  post.tags.map(tag => {
                        return <button key={tag} onClick={(e) => {
                            removeTag(e.target.innerHTML);
                        }} className="tag">{tag}</button>
                    }) 
                    }
                </div>   
{///////////////////////   body-input    //////////////////////
}
                <div className="grow-wrap">
                    <textarea onChange={handleBody} name="inputPost" ref={bodyRef} id="inputPost" value={post.body} cols="100%" rows="10" onInput={(e) => {
                        e.target.parentNode.dataset.replicatedValue = e.target.value
                    } }></textarea>

                </div>
                
            </div>


            <div className="display-wrapper">
                <div className="dot-wrapper">
                    <div className="dot"></div>
                    <div className="dot"></div>
                    <div className="dot"></div>
                </div>
                <div className="display">
                    <div className="post">
                        <div className="post-title">
                            {post.title || "Add title"}
                        </div>
                        <div className="post-tags">
                            { post.tags.map(tag => {
                                    return <button key={tag + "diplay"} className="tag">{tag}</button>
                                }) 
                            }
                        </div>
                        <div className="flex"> 
                            <div className="date" id="date">
                                {_date()}
                            </div>
                            <div className="author">
                                Author: {post.author}
                            </div>
                        </div>
                        <hr/>
                        <div className="post-content" id="post-content">
                            <pre>
                                { post.body || "Add post body"}
                            </pre>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
 
export default CreatPost;