import React from 'react';
import Post from "./post"
import useFetch from './useFetch';

function PostDate() {
    let dateID = window.location.pathname.split('/')
    dateID = dateID[dateID.length - 1];

    const posts = useFetch(`/posts/date/${dateID}`)

    let _date = new Date(dateID);
    let d = _date.toUTCString();
    let date = d.slice(0, 16);
    return (
        <div className="content">
            <h2 className="h2-heading" >{date}</h2>
           {
                posts.length === 0 ? <div className="circle-loader"></div> : posts.map(post => <Post post={post} key={post._id} />)
            }
        </div>
    )
}

export default PostDate;
