import React from 'react';

function Alert(props) {

    function getStyle() {
        let style = {
            position: "fixed",
            zIndex: +3,
            minWidth: "200px",
            display: "block",
            minHeight: "60px",
            padding: "10px",
            boxSizing: "border-box",
            borderRadius: "7px",
            marginLeft: "-100px",
            top: "20px",
            left: "50%",
            fontWeight: 500
        };
        
        switch (props.variant) {
            case "error":
                style = {...style, color: "var(--red)", backgroundColor: "var(--light-red)"};
                break;
        
            case "warning":
                style = {...style, color: "var(--blue)", backgroundColor: "var(--light-blue)"};
                break;
        
            case "success":
                style = {...style, color: "var(--green)", backgroundColor: "var(--light-green)"};
                break;
        
            default:
                style = {...style, color: "black", backgroundColor: "gray"}
                break;
        }
        return style;
    }
    let h3 = {margin: "0px", padding: "0px", width: "100%", textAlign: "center"}
    return (
        <div className="alert" style={getStyle()}>
            {props.variant === "error" && <h3 style={h3}>Error</h3>}
            {props.variant === "warning" && <h3 style={h3}>Warning</h3>}
            {props.variant === "success" && <h3 style={h3}>Success</h3>}
            {props.children}
        </div>
    )
}

export default Alert
