import React, { useState, useEffect } from 'react';
import { 
  BrowserRouter as Router,
  Switch,
  Route } from "react-router-dom";
import Header from './components/header';
import SideNav from './components/sideNav';
import Footer from './components/footer';
import CreatePost from './components/createPost';
import MyPost from './components/myPost';
import CompletePost from './components/completePost';
import Home from './components/home';
import Dashboard from './components/dashboard';
import Posts from './components/posts';
import PostDate from './components/postDate';
import './global.css';
import './components/styles/main.css';
import MainPost from './components/mainPost';
import TagPosts from './components/tagPosts';
import Tags from './components/tags';
import Login from './components/login';
import Signup from './components/signup';




function App(){
  const [toggle, setToggle] = useState(false);
  const [toggleSearchPanel, setToggleSearchPanel] = useState(false);
  function toggleSideNav() {
    setToggle(!toggle);
  }


  function checkSize() {
    if (window.innerWidth >= 700) {
      setToggle(true);
      setToggleSearchPanel(true)
    }else {
      setToggle(false);
      setToggleSearchPanel(false)
    }
  }
  useEffect(() => {
    if (window.innerWidth >= 700) {
      setToggle(true)
      setToggleSearchPanel(true)
    }
    window.addEventListener('resize', checkSize);
    return () => {
      window.removeEventListener('resize', checkSize);
    }
  }, [toggle, toggleSearchPanel]);

  return (
    <Router>
      <Header toggle={toggle} toggleSearchPanel={toggleSearchPanel} setToggleSearchPanel={setToggleSearchPanel}  toggleSideNav={toggleSideNav} />
      <SideNav toggle={toggle} setToggle={setToggle} />
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/createpost">
          <div className="main">
            <CreatePost  />
          </div>
          <Footer />
        </Route>
        <Route exact path="/editpost/:id">
          <div className="main">
            <CreatePost variant="edit" />
          </div>
          <Footer />
        </Route>
        <Route exact path="/settings">
          <div className="main">
            <CreatePost />
          </div>
        </Route>
        <Route exact path="/auth/login">
          <div className="main">
            <Login />
          </div>
          <Footer />
        </Route>
        <Route exact path="/auth/signup">
          <div className="main">
            <Signup />
          </div>
          <Footer />
        </Route>
        <Route exact path="/statistics">
          <div className="main">
            <CreatePost />
          </div>
          <Footer />
        </Route>
        <Route exact path="/mypost">
          <div className="main">
            <MyPost />
          </div>
          <Footer />
        </Route>
        <Route exact path="/tags">
          <div className="main">
            <Tags />
          </div>
          <Footer />
        </Route>
        <Route exact path="/tags/:tag">
          <div className="main">
            <TagPosts />
          </div>
          <Footer />
        </Route>
        <Route exact path="/posts">
          <div className="main">
            <Posts />
          </div>
          <Footer />
        </Route>
        <Route exact path="/posts/:id">
          <div className="main">
            <MainPost />
          </div>
          <Footer />
        </Route>
        <Route exact path="/posts/date/:date">
          <div className="main">
            <PostDate />
          </div>
          <Footer />
        </Route>
        <Route exact path="/mypost/complete">
            <div className="main">
              <CompletePost variant="complete" />
            </div>
            <Footer />
        </Route>
        <Route exact path="/mypost/incomplete">
            <div className="main">
              <CompletePost variant="incomplete" />
            </div>
            <Footer />
        </Route>
        <Route exact path="/admin/dashboard">
            <div className="main">
              <Dashboard />
            </div>
            <Footer />
        </Route>
        <Route>
          <div className="main">
            <h2 className="h2-heading">404 not found</h2>
          </div>
          <Footer />
        </Route>
      </Switch> 
      <Footer />
    </Router>
  )
}
export default App;
