import React from 'react';
import {Link} from 'react-router-dom';

function Tag({tag}) {
    return (
        <Link to={`/tags/${tag}`}>
            <button className="post-tag">
                {tag}
            </button>
        </Link>
    )
}


function Post({post}) {
    
    function todate(date) {
        let d = new Date(date);
        return d.toUTCString();
    }


    return (
        <div className="container">
            <div className="post">
                <div className="post-title">
                    {post.title}
                </div>
                <div className="flex">
                    <div className="post-tags">
                        {
                            post.tags.map(tag => {
                                return <Tag key={tag} tag={tag} />
                            })
                        }
                    </div>
                    <div className="author" style={{marginTop: 15}}>
                        Author: {post.author}
                    </div>
                </div>
                <div className="post-punch-line">
                    {post.body.slice(0, 200) + "...."}
                </div>
                <div className="flex">
                    <div className="date">
                        {todate(post.date)}
                    </div>
                    <div className="read-more">
                        <Link to={`/posts/${post._id}`}>
                            read-more
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Post
