import { Link } from "react-router-dom";
import './styles/myPost.css';



function MyPost() {
    const SVGWidth = '120';
    const SVGHeight = '120';
    return (
        <div>
            <div>
                <h1 style={{width: "100%", fontWeight: 400, textAlign: "center"}} >My post</h1>
                <div className="main-list">
                    <Link to='mypost/incomplete'>
                        <div className="category">
                            <div className="category-image">
                                <svg width={SVGWidth} className="svg" height={SVGHeight} viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg" data-svg="server"><rect x="3" y="3" width="1" height="2"></rect><rect x="5" y="3" width="1" height="2"></rect><rect x="7" y="3" width="1" height="2"></rect><rect x="16" y="3" width="1" height="1"></rect><rect x="16" y="10" width="1" height="1"></rect><circle fill="none" stroke="white" cx="9.9" cy="17.4" r="1.4"></circle><rect x="3" y="10" width="1" height="2"></rect><rect x="5" y="10" width="1" height="2"></rect><rect x="9.5" y="14" width="1" height="2"></rect><rect x="3" y="17" width="6" height="1"></rect><rect x="11" y="17" width="6" height="1"></rect><rect fill="none" stroke="white" x="1.5" y="1.5" width="17" height="5"></rect><rect fill="none" stroke="white" x="1.5" y="8.5" width="17" height="5"></rect></svg>
                            </div>
                            <div className="category-text">incomplete posts</div>
                        </div>
                    </Link>
                    <Link to='mypost/complete'>
                    <div className="category">
                        <div className="category-image">
                            <svg width={SVGWidth} className="svg" height={SVGHeight} viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg" data-svg="server"><rect x="3" y="3" width="1" height="2"></rect><rect x="5" y="3" width="1" height="2"></rect><rect x="7" y="3" width="1" height="2"></rect><rect x="16" y="3" width="1" height="1"></rect><rect x="16" y="10" width="1" height="1"></rect><circle fill="none" stroke="white" cx="9.9" cy="17.4" r="1.4"></circle><rect x="3" y="10" width="1" height="2"></rect><rect x="5" y="10" width="1" height="2"></rect><rect x="9.5" y="14" width="1" height="2"></rect><rect x="3" y="17" width="6" height="1"></rect><rect x="11" y="17" width="6" height="1"></rect><rect fill="none" stroke="white" x="1.5" y="1.5" width="17" height="5"></rect><rect fill="none" stroke="white" x="1.5" y="8.5" width="17" height="5"></rect></svg>
                        </div>
                        <div className="category-text">completed posts</div>
                    </div>

                    </Link>
                </div>
            </div>
        </div>
    );
}

export default MyPost;
