import React, { useRef, useState, useContext } from 'react';
import "./styles/auth.css";
import axios from 'axios';
import { ApiURL } from './urls';

function Signup() {
    const emailRef = useRef(null);
    const passwordRef = useRef(null);
    const userNameRef = useRef(null);
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [userName, setUserName] = useState("");
    const url = useContext(ApiURL);

    function handleSubmit(e) {
        e.preventDefault();
        axios.post(url + "/auth/login")
          .then(res => alert(res.data))
          .catch(err => console.log(err))
    }
    

    return (
        <form className="auth-form" onSubmit={handleSubmit}>
            <h2 className="h2-heading" style={{marginTop: 0}}>Signup</h2>

            <label htmlFor="username" className="auth-label">Username:</label>

            <input className="auth-input" ref={userNameRef} type="text" name="username" onChange={e => setUserName(userNameRef.current.value)} value={userName} placeholder="Input your username"/>

            <label className="auth-label" htmlFor="email">Email:</label>

            <input className="auth-input" ref={emailRef} type="email" name="email" onChange={e => setEmail(emailRef.current.value)} value={email} placeholder="example@email.com"/>


            <label className="auth-label" htmlFor="password">Password:</label>

            <input className="auth-input" type="password" name="password" ref={passwordRef} onChange={e => setPassword(passwordRef.current.value)} value={password} placeholder="password should be more than 8 character" />


            <button className="auth-submit">Signup</button>
        </form>
    )
}

export default Signup;
