import React, { useState, useEffect } from 'react';
import Post from "./post"
import useFetch from './useFetch';

function TagPosts() {
    let tagName = window.location.pathname.split('/');
    tagName = tagName[tagName.length - 1];
    const [url, setUrl] = useState(`/tags/${tagName}`);
    const [state, setState] = useState(1)

    useEffect(() => {
        let isMounted = true;
        let t = window.location.pathname.split('/');
        t = `/tags/${t[t.length - 1]}`
        if (url === t) {
            return
        }else {
            if (isMounted) {
                setUrl(t);
            }
        }
        return () => isMounted = false;
    },[state, url])
    
    useEffect(() => {
        let isMounted = true;
        setTimeout(() => {
            if (isMounted) {
                setState(state + 1);
            }
        }, 100);
        return () => isMounted = false;
    }, [state])

    const posts = useFetch(url);

    return (
        <div className="content">
            <h2 className="h2-heading" >{tagName}</h2>
           {
                posts.length === 0 ? <div className="circle-loader"></div> : posts.map(post => <Post post={post} key={post._id} />)
            }
        </div>
    )
}

export default TagPosts;