import React from 'react';
import {Link} from "react-router-dom";
import "./styles/posts.css";
import useFetch from './useFetch';


function Posts() {
    const dates = useFetch('/posts/date');


    function fDate(date) {
        let d = new Date(date);
        d = d.toUTCString();
        d = d.slice(5, d.length-13);
        return d;
    }
    return (
        <div className="content">
            <h2 className="h2-heading">Posts</h2>
            <ul className="posts-posts">
                {
                    dates.length === 0 ? <div className="circle-loader"></div> : dates.map(date => <li key={date} className="posts-post"> <Link to={`/posts/date/${date}`} className="date-link">{fDate(date)}</Link> </li>)
                }
            </ul>
        </div>
    )
}

export default Posts
