import React from 'react';
import { Link } from "react-router-dom";
import './styles/header.css';
import SearchPanel from './searchPanel';

function Header({ toggle, toggleSideNav, toggleSearchPanel, setToggleSearchPanel}){

    
    return (
        <div className="header">
            <SearchPanel toggleSearchPanel={toggleSearchPanel} setToggleSearchPanel={setToggleSearchPanel} />
            <ul className="nav">
                <li className="launch-side-nav-wrapper">
                    <button onClick={toggleSideNav} className="launch-side-nav">
                        { toggle ? <span style={{fontSize: '1.7rem', fontWeight: 400, marginLeft: 10}}>X</span> : <span dangerouslySetInnerHTML={{__html: '&#9776;'}}></span> }
                        
                    </button>
                </li>
                <li className="logo">
                    <Link to="/" className="remove-link-style">Dare Dev Blog</Link>
                </li>
                
                <li className="auth">
                    <Link to="/auth/login">
                        <button className="nav-btn launch-modal">Login</button>
                    </Link>
                    <Link to="/auth/signup">
                        <button className="nav-btn launch-modal">Signup</button>
                    </Link>
                </li>

                <li className="launch-search-panel" onClick={e => setToggleSearchPanel(!toggleSearchPanel)}>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z"/></svg>
                </li>
            </ul>
        </div>
    )
}
 
export default Header;