import React, { useState, useRef, useContext, useEffect} from 'react';
import {Link} from "react-router-dom";
import './styles/myPost.css';
import axios from "axios";
import {ApiURL} from './urls';
import Alert from './alert';


function Post({title, date, id, rerenderParent}) {
    const [show, setShow] = useState(false);
    const [prompt, setPrompt] = useState(false)
    const editPanelRef = useRef(null);
    const dotRef = useRef(null);
    const apiurl = useContext(ApiURL);
    useEffect(() => {
        if (show) {
            editPanelRef.current.style.display = "block";
        }else {
            editPanelRef.current.style.display = "none";
        }
    }, [show]);



    function handleDelete() {
        axios.delete(`${apiurl}/posts/${id}`)
          .then(res => {
              console.log(res.data);
              rerenderParent(id);
            })
          .catch(err => {
              console.log(err.message);
          });
    }
    return (
        <>
        {
            prompt && 
            <div className="prompt">
                Are You Sure You Want To Delete This Post
                <div className="flex">
                    <button onClick={e => setPrompt(false)}>Cancel</button>
                    <button onClick={handleDelete}>Delete</button>
                </div>
            </div>
        }
        <div className="post" style={{
            position: "relative",
            borderRadius: 3,
            boxShadow: "1px 1px 7px gray",
            marginBottom: 25,
            paddingBottom: 5
        }}>
            <ul ref={dotRef} onClick={e => setShow(true)} className="dot-here-wrapper">
                <li className='dot-here'></li>
                <li className='dot-here'></li>
                <li className='dot-here'></li>
            </ul>
            <div className="title"><Link to={`/posts/${id}`} style={{textDecoration: "none", display: "block", color: "black"}} >{title}</Link></div>
            <div className="date">Date: {date}</div>
            <div onMouseOut={
                e => setTimeout(() => {
                    setShow(false)
                }, 3000)
            } className="edit-panel" ref={editPanelRef}>
                <Link style={{textDecoration: "none", color: "black", padding: "0px"}} to={`/editpost/${id}`}>
                    <div>Edit</div>
                </Link>
                <div onClick={e => setPrompt(true)}>
                    Delete
                </div>
            </div>
        </div>
        </>
    )
}


function CompletePost({variant}) {
    const [posts, setPosts] = useState([]);
    const [alert, setAlert] = useState({variant: "", message: "", show: false});
    const url = useContext(ApiURL);
    useEffect(() => {
        axios.get(`${url}/posts`)
        .then(res => setPosts(res.data))
        .catch(err => {console.log(err); showAlert("error", `${err}`)});
        if (variant === "complete") {
        } else if (variant === "complete") {
            axios.get(`${url}/posts`)
            .then(res => setPosts(res.data))
            .catch(err => {console.log(err); showAlert("error", `${err}`)});
        }
    }, [url, variant]);
    useEffect(() => {
        if (alert.show) {
            setTimeout(() => {
                setAlert({variant: "", show: false, message: ""})
            }, 5000);
        }
    }, [alert]);

    function showAlert(variant, message) {
        setAlert({variant, message, show: true})
    }
    let style = {
        width: "100%",
        fontWeight: 400, 
        textAlign: "center",
        textTransform: "capitalize"
    }
    function rerenderParent(id) {
        let p = posts.filter(post => post._id !== id);
        setPosts(p);
    }

    return (
        <div className="post-list" style={{ maxWidth: '800px', margin: 'auto', marginTop: "90px"}}>
            {alert.show && <Alert variant={alert.variant}>
                {alert.message}
            </Alert>}
            {variant === "complete" && <h1 style={style} >completed posts</h1>}
            {variant === "incomplete" && <h1 style={style} >incomplete posts</h1>}

            {posts.map(post => {
                return <Post rerenderParent={rerenderParent} key={post._id} id={post._id} title={post.title} date={post.date}></Post>
            })}
            
        </div>
    )
}

export default CompletePost;