import React from 'react';
import "./styles/home.css";
import Post from './post';
import useFetch from './useFetch';
import { Link } from 'react-router-dom/cjs/react-router-dom.min';


function Index() {
    const posts = useFetch('/posts/indexposts');
    return (
        <div className="main" style={{top: 0}}>
            <div className="content">
                <h2 className="h2-heading">Latest Posts</h2>
                {posts.map(post => <Post key={post._id} post={post} />)}
            </div>
        </div>
    )
}


function Home() {
    return (
        <>
            <div className="hero home-hero">
                <div className="flex-home home-hero-box hero-box">
                    <div className="box">
                        <div className="designed-box">
                            <div className="designed-box"></div>
                        </div>
                    </div>
                    <h2 style={{width: "200px"}}>welcome to Dare Dev Blog</h2>
                </div>
                <div style={{width: "140px"}} className="flex side-auth">
                    <Link to="/auth/login">
                        <button className="nav-btn launch-modal">Login</button>
                    </Link>
                    <Link to="/auth/signup">
                        <button className="nav-btn launch-modal">Signup</button>
                    </Link>
                </div>
            </div>
            <Index />
        </>
    );
}

export default Home;
