import React, { useRef, useEffect } from 'react';
import './styles/sideNav.css';
import { Link } from "react-router-dom";


function SideNav({toggle, setToggle}) {
    const sideNavRef = useRef(null);
    useEffect(() => {
        if (toggle) {
            sideNavRef.current.style.left = '0px'
        }else {
            sideNavRef.current.style.left = '-100%'
        }
    })
    return ( 
        <div ref={sideNavRef} className="side-nav">
            <div className="side-auth">
                <Link to="/auth/login" onClick={e => setToggle(!toggle)}>
                    <button className="nav-btn launch-modal">Login</button>
                </Link>
                <Link to="/auth/signup" onClick={e => setToggle(!toggle)}>
                    <button className="nav-btn launch-modal">Signup</button>
                </Link>
            </div>
            <ul className="links">
                <li onClick={e => setToggle(!toggle)} className="link">
                    <Link to="/">home</Link>
                </li>
                <li onClick={e => setToggle(!toggle)} className="link">
                    <Link to="/posts">posts</Link>
                </li>
                <li onClick={e => setToggle(!toggle)} className="link">
                    <Link to="/tags">tags</Link>
                </li>
                <li onClick={e => setToggle(!toggle)} className="link">
                    <Link to="/admin/dashboard">dashboard</Link>
                </li>
                <li onClick={e => setToggle(!toggle)} className="link">
                    <a href="/about">about</a>
                </li>
            </ul>
        </div>
        );
}
 
export default SideNav;