import React, { useRef, useEffect, useContext, useState} from 'react';
import "./styles/searchPanel.css";
import { ApiURL } from './urls';
import axios from 'axios';
import { Link } from 'react-router-dom/cjs/react-router-dom.min';

function SearchPanel({toggleSearchPanel, setToggleSearchPanel}) {
    const searchTextRef = useRef(null);
    const closeSearchPanelRef = useRef(null);
    const searchPanelRef = useRef(null);
    const [searchText, setSearchText] = useState("");
    const [searchResult, setSearchResult] = useState([]);
    const [loading, setLoading] = useState(true);
    const [toggleSearchResultPanel, setToggleSearchResultPanel] = useState(false);
    // const [urlChanged, seturlChanged] = useState();

    const api = useContext(ApiURL);
    useEffect(() => {
        function fix() {
            console.log(window.location.href)
            console.log("it changed");
            initClose();
        }
        if (toggleSearchPanel) {
            searchPanelRef.current.style.top = "0px";
        }else {
            initClose();
            searchPanelRef.current.style.top = "-100%";
        }
        window.addEventListener("popstate", fix);
        return () => window.removeEventListener("popstate", fix);
    }, [toggleSearchPanel]);
    

    function initClose() {
        setToggleSearchPanel(false);
        setSearchText("");
        setToggleSearchResultPanel(false);
        setLoading(true);
    }
    
    function initClear() {
        setSearchText("");
        setToggleSearchResultPanel(false);
        setLoading(true);
    }

    function handleSubmit(e) {
        e.preventDefault();
        setToggleSearchResultPanel(true);
        axios.get(`${api}/posts/search?q=${searchText}`)
          .then(res => {
              setSearchResult(res.data);
              setLoading(false);
          })
          .catch(err => console.log(err));
    }



    return (
        <div id="search-panel" ref={searchPanelRef}>
            <div className="search-post">
                <form onSubmit={handleSubmit}>
                    <input type="text" name="search-input" placeholder="Search for a post" id="search-input" onChange={e => setSearchText(searchTextRef.current.value)} value={searchText} ref={searchTextRef}/>

                    <button type="button" id="clear-search-btn" onClick={e => initClear()}>X</button>

                    <button type="submit" id="search-btn">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z"/></svg>
                    </button>
                </form>
                {
                    toggleSearchResultPanel &&
                    <div id="search-list">
                        <button onClick={e => setToggleSearchResultPanel(false)} className="close-btn">x</button>
                        <div className="posts-search-list">
                            {
                                loading && 
                                <div className="loader">
                                    <ul>
                                        <li className="one"></li>
                                        <li className="two"></li>
                                        <li className="three"></li>
                                    </ul>
                                </div>
                            }
                            <div className="post-search-result">
                                <div className="title-search">
                                    {
                                        searchResult.map(data => {
                                            return <Link key={data.id} to={`/posts/${data.id}`} onClick={e => initClose()}>
                                                <div className="title-search">
                                                    {data.title}
                                                </div>
                                            </Link>
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                }
            </div>

            <div id="close-search-panel" onClick={e => initClose()} ref={closeSearchPanelRef}>
                <div className="up-arrow"></div>
            </div>
        </div>
    )
}

export default SearchPanel
