import React, {useState, useContext, useEffect} from 'react';
import {ApiURL} from "./urls";
import axios from 'axios';
import {Link} from 'react-router-dom';


function Tag({tag}) {
    return (
        <Link to={`/tags/${tag}`}>
            <button className="post-tag">
                {tag}
            </button>
        </Link>
    )
}


function MainPost() {
    let postID = window.location.pathname.split('/')
    postID = postID[postID.length - 1];

    const [post, setPost] = useState(null);

    const apiurl = useContext(ApiURL);

    useEffect(() => {
        let isMounted = true;
        axios.get(apiurl + `/posts/${postID}`)
         .then(res => {
             if (res.data.hasOwnProperty("error")) {
                 alert("was an error fetching the posts")
             }else {
                 if (isMounted){
                    setPost(res.data)
                 }
             }
         })
         .catch(err => alert("there was an err\n" + err));
        return () => {
            isMounted = false;
        }
         
    }, [postID, apiurl]);
    
    function todate(date) {
        let d = new Date(date);
        return d.toUTCString();
    }
    return (
        <div className="content">
            {
                post === null ? 
                <div className="circle-loader"></div> :
                <>
                    <div className="post-title">
                        {post.title}
                    </div>
                    <div className="post-tags">
                        {
                            post.tags.map(tag => <Tag key={tag} tag={tag} />)
                        }
                    </div>
                    <div className="flex"> 
                        <div className="date">
                            {todate(post.date)}
                        </div>
                        <div className="author">
                            Author: {post.author}
                        </div>
                    </div>
                    <hr />
                    <div className="post-content">
                        <pre>{post.body}</pre>
                    </div>
                </>
            }
        </div>
    )
}

export default MainPost
