import React from 'react';
import {Link} from "react-router-dom";
import "./styles/posts.css";
import useFetch from './useFetch';


function Tags() {
    const tags = useFetch("/tags");

    return (
        <div className="content">
            <h2 className="h2-heading">Tags</h2>
            <ul className="posts-posts">
                {
                    tags.length === 0 ? 
                    <div className="circle-loader"></div> : 
                    tags.map(tag => <li key={tag} className="posts-post"> <Link to={`/tags/${tag}`} className="date-link">{tag}</Link> </li>)
                }
            </ul>
        </div>
    )
}

export default Tags;
